import React from "react";
import styled from "styled-components";
import Grid from "@mui/material/Grid";
import DoneOutlineIcon from "@mui/icons-material/DoneOutline";
import OnsikgoSlider from "./OnsikgoSlider";
const Container = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: center;
  background-color: #fff;
`;
const Title = styled.div`
  font-family: "MaplestoryOTFBold";
  font-size: 2.5rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
`;

const Content = styled.p`
  line-height: 1.4rem;
  padding-bottom: 0.5rem;
`;

const Important = styled.span`
  color: red;
`;

const Mojor = styled.span`
  font-weight: bold;
`;

const ContentBox = styled.div`
  display: flex;
  margin-top: 1rem;
  align-items: center;
  width: 100%;
`;

const ContentTag = styled.div`
  width: 20%;
  font-weight: bold;
`;

const ContentTag2 = styled.div`
  line-height: 1.4rem;
  width: 70%;
  word-break: break-all;
`;
function Onsikgo() {
  const goGithub = () => {
    window.open("https://github.com/jangchangwan/OnSikGo-1");
  };

  return (
    <Container>
      <Title>   (  Go)</Title>
      <p>
        2022.07 ~ 2022.08 ( 6 , <Mojor> </Mojor> )
      </p>
      <Grid container sx={{ marginTop: "2rem", width: "100%" }}>
        <Grid item xs={12} sm={12} md={6} sx={{ width: "50%" }}>
          <OnsikgoSlider />
        </Grid>
        <Grid item xs={12} sm={12} md={6}>
          <Content>
            <Mojor>
                  ,     
            </Mojor>
               ,     
            .
          </Content>
          <Content>
                    
            ,        
                  .
          </Content>

          <Content>
            Front-End   <Important>Vue</Important>
              <Important>Vuex</Important>  
                   
            .
          </Content>
          <Content>
             <Important>Kakao-map</Important>    
               <Important>Kakao-login</Important> 
             Kakao API     .
          </Content>
          <hr />
          <ContentBox>
            <DoneOutlineIcon sx={{ marginRight: "0.5rem" }} />
            <ContentTag></ContentTag>
            <ContentTag2>
                ,   , SNS ,  
               
            </ContentTag2>
          </ContentBox>
          <ContentBox>
            <DoneOutlineIcon sx={{ marginRight: "0.5rem" }} />
            <ContentTag>Github</ContentTag>
            <ContentTag2
              onClick={goGithub}
              style={{ cursor: "pointer", color: "blue" }}
            >
              https://github.com/jangchangwan/OnSikGo-1
            </ContentTag2>
          </ContentBox>
          <ContentBox>
            <DoneOutlineIcon sx={{ marginRight: "0.5rem" }} />
            <ContentTag>FrontEnd</ContentTag>
            <ContentTag2>Vue, Vuex, Vuetify,JavaScript, </ContentTag2>
          </ContentBox>
          <ContentBox>
            <DoneOutlineIcon sx={{ marginRight: "0.5rem" }} />
            <ContentTag>BackEnd</ContentTag>
            <ContentTag2>SpringBoot, Gradle</ContentTag2>
          </ContentBox>
          <ContentBox>
            <DoneOutlineIcon sx={{ marginRight: "0.5rem" }} />
            <ContentTag>DataBase</ContentTag>
            <ContentTag2>MySQL, Redis</ContentTag2>
          </ContentBox>
          <ContentBox>
            <DoneOutlineIcon sx={{ marginRight: "0.5rem" }} />
            <ContentTag>Server</ContentTag>
            <ContentTag2>Docker, Jenkins, Amazon EC2, Amazon S3</ContentTag2>
          </ContentBox>
        </Grid>
      </Grid>
    </Container>
  );
}

export default Onsikgo;
