import React from "react";
import styled from "styled-components";
import Grid from "@mui/material/Grid";
import DoneOutlineIcon from "@mui/icons-material/DoneOutline";
import AbuyZSlider from "./AbuyZSlider";
const Container = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: center;
  background-color: #fff;
`;
const Title = styled.div`
  font-family: "MaplestoryOTFBold";
  font-size: 2.5rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
`;

const Content = styled.p`
  line-height: 1.4rem;
  padding-bottom: 0.5rem;
`;

const Important = styled.span`
  color: red;
`;

const Mojor = styled.span`
  font-weight: bold;
`;

const ContentBox = styled.div`
  display: flex;
  margin-top: 1rem;
  align-items: center;
  width: 100%;
`;

const ContentTag = styled.div`
  width: 20%;
  font-weight: bold;
`;

const ContentTag2 = styled.div`
  line-height: 1.4rem;
  width: 70%;
  word-break: break-all;
`;
function Abuyz() {
  const goGithub = () => {
    window.open("https://github.com/jangchangwan/AbuyZ");
  };
  return (
    <Container>
      <Title>Abuyz</Title>
      <p>
        2022.10 ~ 2022.11 ( 6 , <Mojor> </Mojor>{" "}
        )
      </p>
      <Grid container sx={{ marginTop: "2rem" }}>
        <Grid item xs={12} sm={12} md={6}>
          <div style={{ width: "90%", marginBottom: "3rem" }}>
            <AbuyZSlider></AbuyZSlider>
          </div>
        </Grid>
        <Grid item xs={12} sm={12} md={6}>
          <Content>
            <Mojor>
              AbuyZ  ,      .
            </Mojor>
          </Content>
          <Content>
                   
            .  40:1     
                SI  .
            6   .    ? 
               ? AbuyZ   
            .
          </Content>

          <Content>
            React   <Important>Next.js</Important>
              <Important>SSR, CSR, SSG</Important> 
               .
          </Content>
          <Content>
                <Important>Recoil</Important> 
                .
          </Content>
          <hr />
          <ContentBox>
            <DoneOutlineIcon sx={{ marginRight: "0.5rem" }} />
            <ContentTag></ContentTag>
            <ContentTag2>
              <p>FO :  , ,  , ,  </p>
              <p>BO :  , , , ,  </p>
            </ContentTag2>
          </ContentBox>
          <ContentBox>
            <DoneOutlineIcon sx={{ marginRight: "0.5rem" }} />
            <ContentTag>Github</ContentTag>
            <ContentTag2
              onClick={goGithub}
              style={{ cursor: "pointer", color: "blue" }}
            >
              https://github.com/jangchangwan/AbuyZ
            </ContentTag2>
          </ContentBox>
          <ContentBox>
            <DoneOutlineIcon sx={{ marginRight: "0.5rem" }} />
            <ContentTag>FrontEnd</ContentTag>
            <ContentTag2>Next.js, Recoil, Chart.js, JavaScript</ContentTag2>
          </ContentBox>
          <ContentBox>
            <DoneOutlineIcon sx={{ marginRight: "0.5rem" }} />
            <ContentTag>BackEnd</ContentTag>
            <ContentTag2>SpringBoot, Gradle</ContentTag2>
          </ContentBox>
          <ContentBox>
            <DoneOutlineIcon sx={{ marginRight: "0.5rem" }} />
            <ContentTag>DataBase</ContentTag>
            <ContentTag2>MySQL, Redis</ContentTag2>
          </ContentBox>
          <ContentBox>
            <DoneOutlineIcon sx={{ marginRight: "0.5rem" }} />
            <ContentTag>Server</ContentTag>
            <ContentTag2>Docker, Jenkins, Amazon EC2, Amazon S3</ContentTag2>
          </ContentBox>
        </Grid>
      </Grid>
    </Container>
  );
}

export default Abuyz;
