import React from "react";
import styled from "styled-components";
import Grid from "@mui/material/Grid";
import DoneOutlineIcon from "@mui/icons-material/DoneOutline";
import ChuAniOneSlider from "./ChuAniOneSlider";

const Container = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: center;
  background-color: #fff;
`;
const Title = styled.div`
  font-family: "MaplestoryOTFBold";
  font-size: 2.5rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
`;

const Content = styled.p`
  line-height: 1.4rem;
  padding-bottom: 0.5rem;
`;

const Important = styled.span`
  color: red;
`;

const Mojor = styled.span`
  font-weight: bold;
`;

const ContentBox = styled.div`
  display: flex;
  margin-top: 1rem;
  align-items: center;
  width: 100%;
`;

const ContentTag = styled.div`
  width: 20%;
  font-weight: bold;
`;

const ContentTag2 = styled.div`
  line-height: 1.4rem;
  width: 70%;
  word-break: break-all;
`;
function ChuAniOne() {
  const goGithub = () => {
    window.open("https://github.com/jangchangwan/ChuAniOne");
  };
  return (
    <Container>
      <Title>Chu AniOne</Title>
      <p>
        2022.08 ~ 2022.09 ( 6 , <Mojor> </Mojor> )
      </p>
      <Grid container sx={{ marginTop: "2rem" }}>
        <Grid item xs={12} sm={12} md={6}>
          <div style={{ width: "90%", marginBottom: "3rem" }}>
            <ChuAniOneSlider />
          </div>
        </Grid>
        <Grid item xs={12} sm={12} md={6}>
          <Content>
            <Mojor>
              ""      
                   
            </Mojor>
            .
          </Content>
          <Content>
            <Important>TypeScript</Important>    
                    
            .
          </Content>
          <Content>
              <Important>React</Important>  
            <Important>Redux</Important>    
                  .
          </Content>
          <Content>
             <Important>Framer-Motion</Important>  
                      ,
            <Important> Socket.io</Important>     
            .
          </Content>
          <hr />
          <ContentBox>
            <DoneOutlineIcon sx={{ marginRight: "0.5rem" }} />
            <ContentTag></ContentTag>
            <ContentTag2>
               , ,   ,   
              ,   ,  
            </ContentTag2>
          </ContentBox>
          <ContentBox>
            <DoneOutlineIcon sx={{ marginRight: "0.5rem" }} />
            <ContentTag>Github</ContentTag>
            <ContentTag2
              onClick={goGithub}
              style={{ cursor: "pointer", color: "blue" }}
            >
              https://github.com/jangchangwan/ChuAniOne
            </ContentTag2>
          </ContentBox>
          <ContentBox>
            <DoneOutlineIcon sx={{ marginRight: "0.5rem" }} />
            <ContentTag>FrontEnd</ContentTag>
            <ContentTag2>
              React, Redux, TypeScript, soket.io, styled-component,
              framer-motion, MUI
            </ContentTag2>
          </ContentBox>
          <ContentBox>
            <DoneOutlineIcon sx={{ marginRight: "0.5rem" }} />
            <ContentTag>BackEnd</ContentTag>
            <ContentTag2>Django, SpringBoot</ContentTag2>
          </ContentBox>
          <ContentBox>
            <DoneOutlineIcon sx={{ marginRight: "0.5rem" }} />
            <ContentTag>DataBase</ContentTag>
            <ContentTag2>MySQL, Redis</ContentTag2>
          </ContentBox>
          <ContentBox>
            <DoneOutlineIcon sx={{ marginRight: "0.5rem" }} />
            <ContentTag>BigData</ContentTag>
            <ContentTag2>
              keras, numpy, pandas, scikit-learn, tensorflow, MongoDB
            </ContentTag2>
          </ContentBox>
        </Grid>
      </Grid>
    </Container>
  );
}

export default ChuAniOne;
